// src/pages/hr/jobs.js
import { useState, useEffect } from "react";
import { supabase } from "lib/supabase";
import { useRouter } from "next/router";
import toast, { Toaster } from "react-hot-toast";
import HRJobSidebar from "@/components/HRJobSidebar";
import HRDashboardHeader from "@/components/HRDashboardHeader";

export default function HRJobBoard({ mode = "light" }) {
    const [jobs, setJobs] = useState([]);
    const [viewMode, setViewMode] = useState("cards");
    const router = useRouter();

    useEffect(() => {
        if (!localStorage.getItem("hr_session")) {
            router.push("/hr/login");
        } else {
            fetchJobs();
        }
    }, [router]);

    const fetchJobs = async () => {
        const { data, error } = await supabase
            .from("job_openings")
            .select("*")
            .order("created_at", { ascending: false });
        if (error) {
            console.error("Error fetching jobs:", error);
            toast.error("Failed to load job openings.");
        } else {
            const updatedJobs = data.map((job) => ({
                ...job,
                is_expired: new Date(job.expires_on) < new Date(),
            }));
            setJobs(updatedJobs);
        }
    };

    const handleSubmitJob = async (jobData) => {
        const { error } = await supabase.from("job_openings").insert([jobData]);
        if (error) {
            console.error("Error submitting job:", error);
            toast.error("Failed to submit job opening.");
            return false;
        } else {
            toast.success("Job opening added successfully!");
            fetchJobs();
            return true;
        }
    };

    const handleDeleteJob = async (jobId) => {
        const { error } = await supabase.from("job_openings").delete().eq("id", jobId);
        if (error) {
            console.error("Error deleting job:", error);
            toast.error("Failed to delete job opening.");
        } else {
            toast.success("Job opening deleted successfully!");
            fetchJobs();
        }
    };

    const handleLogout = () => {
        localStorage.removeItem("hr_session");
        toast.success("Logged out successfully!");
        setTimeout(() => router.push("/hr/login"), 1000);
    };

    return (
        <div
            className={`min-h-screen flex flex-col ${
                mode === "dark" ? "bg-gradient-to-b from-gray-900 to-gray-800" : "bg-gradient-to-b from-gray-50 to-gray-100"
            }`}
        >
            <Toaster position="top-right" reverseOrder={false} />
            <HRDashboardHeader mode={mode} onLogout={handleLogout} />
            <div className="flex flex-1 p-6 gap-6 max-w-7xl mx-auto">
                <HRJobSidebar onSubmitJob={handleSubmitJob} mode={mode} />
                <div className="w-full md:w-2/3">
                    <div className="flex justify-between items-center mb-4">
                        <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                            Current Job Openings
                        </h3>
                        <button
                            onClick={() => setViewMode(viewMode === "cards" ? "table" : "cards")}
                            className={`px-3 py-1 rounded-lg ${mode === "dark" ? "bg-gray-700 text-white" : "bg-gray-200 text-[#231812]"}`}
                        >
                            {viewMode === "cards" ? "Table View" : "Card View"}
                        </button>
                    </div>
                    {jobs.length === 0 ? (
                        <p className={`italic ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                            No job openings yet.
                        </p>
                    ) : viewMode === "cards" ? (
                        <div className="space-y-4">
                            {jobs.map((job) => (
                                <div
                                    key={job.id}
                                    className={`p-4 rounded-lg shadow-md hover:shadow-lg transition duration-200 animate-fade-in ${
                                        mode === "dark" ? "bg-gray-800 text-white" : "bg-white text-[#231812]"
                                    } ${job.is_expired ? "opacity-50" : ""}`}
                                >
                                    <h4 className="text-lg font-semibold">{job.title}</h4>
                                    {job.description && <p className="mt-1">{job.description}</p>}
                                    {job.file_url && (
                                        <a
                                            href={job.file_url}
                                            className="text-[#f05d23] underline hover:text-[#d94f1e] mt-1 inline-block"
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            View File
                                        </a>
                                    )}
                                    <p className={`text-sm mt-2 ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                                        Expires: {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                                    </p>
                                    <button
                                        onClick={() => handleDeleteJob(job.id)}
                                        className="mt-2 px-3 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200"
                                    >
                                        Delete
                                    </button>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <div className="overflow-x-auto">
                            <table
                                className={`w-full rounded-lg shadow-md ${
                                    mode === "dark" ? "bg-gray-800 text-white" : "bg-white text-[#231812]"
                                }`}
                            >
                                <thead>
                                <tr
                                    className={`${
                                        mode === "dark" ? "bg-gray-700" : "bg-gray-100"
                                    }`}
                                >
                                    <th className="p-3 text-left">Title</th>
                                    <th className="p-3 text-left">Description</th>
                                    <th className="p-3 text-left">File</th>
                                    <th className="p-3 text-left">Expires</th>
                                    <th className="p-3 text-left">Actions</th>
                                </tr>
                                </thead>
                                <tbody>
                                {jobs.map((job) => (
                                    <tr
                                        key={job.id}
                                        className={`border-b ${job.is_expired ? "opacity-50" : ""} ${
                                            mode === "dark" ? "border-gray-700" : "border-gray-200"
                                        }`}
                                    >
                                        <td className="p-3">{job.title}</td>
                                        <td className="p-3">{job.description || "N/A"}</td>
                                        <td className="p-3">
                                            {job.file_url ? (
                                                <a
                                                    href={job.file_url}
                                                    className="text-[#f05d23] underline hover:text-[#d94f1e]"
                                                    target="_blank"
                                                    rel="noopener noreferrer"
                                                >
                                                    View
                                                </a>
                                            ) : (
                                                "N/A"
                                            )}
                                        </td>
                                        <td className="p-3">
                                            {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                                        </td>
                                        <td className="p-3">
                                            <button
                                                onClick={() => handleDeleteJob(job.id)}
                                                className="px-2 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200"
                                            >
                                                Delete
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
            </div>
            <footer
                className={`p-4 text-center text-sm shadow-inner ${
                    mode === "dark" ? "bg-gray-900 text-gray-400" : "bg-white text-gray-500"
                }`}
            >
                Â© {new Date().getFullYear()} Growthpad Consulting Group. All rights reserved.
            </footer>
        </div>
    );
}